import React, {Component} from 'react';
import RelatedNews from '../../components/LeftSidebar/RelatedNews';
import SearchForm from '../../components/LeftSidebar/SearchForm';
import TagSearchNews from '../../components/LeftSidebar/TagSearchNews';
import ItemNews from "../../components/News/ItemNews";
import NewsDetail from "../../components/News/NewsDetail";
import Breadcrumb from '../../components/Breadcrumb';
import {connect} from "react-redux";
import * as Actions from "../../actions";
import Loading from '../../components/Loading';
import {Helmet} from 'react-helmet';
const loadJs = require('loadjs');

class Detail extends Component {
    componentDidMount() {
        loadJs('/js/main.js');
        let idSlug = this.props.match.params.id;
        const arraySlug = idSlug.split('-');
        const id = parseInt(arraySlug[arraySlug.length - 1]);
        this.props.fetchData(id);
    }

    showData = () => {
        if (this.props.data != null && this.props.fetching === false) {
            return (
                <div>
                    <NewsDetail data={this.props.data}/>
                </div>
            )
        } else {
            return <Loading />
        }
    }

    showDataRelated = () => {
        if (this.props.data != null && this.props.fetching === false) {
            return this.props.data.dataRelated.map((item,key) => {
                return (
                    <div key={key} className="col-lg-4 col-md-4 col-sm-6">
                        <ItemNews data={item}/>
                    </div>
                );
            })
        } else {
            return <Loading />
        }

    }

    render() {
        const moduleName = 'Tin tức';
        return (
            <div>
                <Helmet>
                    <title>{this.props.data ? this.props.data.data.name : ''}</title>
                </Helmet>
                <Breadcrumb module={moduleName} title={this.props.data ? this.props.data.data.name : ''}/>
                <section className="blog-details spad">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-4 col-md-5 order-md-1 order-2">
                                <div className="blog__sidebar">
                                    <div className="blog__sidebar__search">
                                        <SearchForm/>
                                    </div>
                                    <div className="blog__sidebar__item">
                                        <RelatedNews data={this.props.dataDefault}/>
                                    </div>
                                    {/*<div className="blog__sidebar__item">*/}
                                        {/*<TagSearchNews/>*/}
                                    {/*</div>*/}
                                </div>
                            </div>
                            <div className="col-lg-8 col-md-7 order-md-1 order-1">
                                {this.showData()}
                            </div>
                        </div>
                    </div>
                </section>

                {/* Related Blog Section Begin */}
                <section className="related-blog spad">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title related-blog-title">
                                    <h2>Tin tức khác</h2>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            {this.showDataRelated()}
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        fetching: state.payloadNewsDetail.fetching,
        id: state.payloadNewsDetail.id,
        data: state.payloadNewsDetail.data,
        error: state.payloadNewsDetail.error,
        dataDefault: state.payloadDataDefault.data
    };
};

const mapDispatchToProps = dispatch => {
    return {
        fetchData: (id) => dispatch(Actions.newsDetailAction.requestNewsDetailData(id))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Detail);