import React, {Component} from 'react';
import {Link} from 'react-router-dom';
class ItemNews extends Component {
    render() {
        const {data} = this.props;
        return (
            <div className="blog__item">
                <div className="blog__item__pic">
                    <img src={data.icon} alt={data.name} />
                </div>
                <div className="blog__item__text">
                    <ul>
                        <li><i className="fa fa-calendar-o" /> {data.date_created}</li>
                    </ul>
                    <h5><Link to={"/news/" + data.name_url + '-' + data.id}>{data.name}</Link></h5>
                    <p>{data.intro}</p>
                    <Link to={"/news/" + data.name_url + '-' + data.id} className="blog__btn">XEM THÊM <span className="arrow_right" /></Link>
                </div>
            </div>
        );
    }
}

export default ItemNews;